<template>
  <div class="guess">
    <div class="guess-head">
      <div>
        <img src="https://imgs.qunarzz.com/piao/fusion/1711/89/ebc329f16c55bb02.png" alt="猜你喜欢">
        <span>猜你喜欢</span>
      </div>
    </div>
    <div class="guess-list" v-for="item in guessList" :key="item.id">
      <router-link :to="'/detail'">
        <div class="guess-item border-bottom">
          <ul class="guess-detail ">
            <li class="detail-img">
              <div class="plan plan1" v-show="item.plan">可订明日</div>
              <div class="plan plan2" v-show="!item.plan">随买随用</div>
              <img :src="item.imgUrl" alt="">
            </li>
            <li class="detail-content">
              <p class="name">{{item.name}}</p>
              <p class="discuss">
                <span class="star-fill">★★★★★</span>
                <span class="star">☆☆☆☆☆</span>
                {{item.discuess}}条评论
              </p>
              <div class="pAp">
                <span class="price"><em class="sign">¥</em><em class="num">{{item.price}}</em>起</span>
                <span class="place">{{item.place}}</span>
              </div>
              <div class="desc" v-if="item.desc">{{item.desc}}</div>
            </li>
          </ul>
        </div>
      </router-link>
    </div>
    <div class="showAll">查看所有产品</div>
  </div>
</template>

<script>
export default {
  name: 'HomeGuess',
  props: {
    guessList: Array
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.guess
  margin-top .22rem
  background-color $bgc
.guess-head
  padding 0.24rem 0.2rem
  img
    width .3rem
  span
    font-size .32rem
    vertical-align middle
.guess-list
  padding-left .2rem
.guess-item
  padding .24rem .2rem .24rem 0
.guess-detail
  display flex
  .detail-img
    position relative
    .plan
      position absolute
      width 1.02rem
      height .38rem
      color #fff
      font-size .24rem
      line-height .38rem
      background-size 100%
    .plan1
      background-image url(https://img1.qunarzz.com/piao/fusion/1802/52/b9080e45b69b4f02.png)
    .plan2
      background-image url(https://img1.qunarzz.com/piao/fusion/1802/20/2ba6d10b17972e02.png)
    img
      width 2rem
.detail-content
  flex 1
  padding .26rem 0 0 .22rem
.name
  font-size .32rem
.discuss
  position relative
  margin-top .18rem
  font-size .24rem
  .star-fill
    position absolute
    top 0
    left 0
.pAp
  display flex
  margin-top .4rem
  justify-content space-between
  .price
    font-size .24rem
    .sign
      color #ff8300
    .num
      font-size .4rem
      color #ff8300
  .place
    width 2rem
    color #616161
    text-align right
    font-size .24rem
    line-height 2
    vertical-align middle
    overhide()
.desc
  display inline-block
  margin-top .54rem
  font-size .24rem
  padding .04rem .1rem
  color: rgb(255, 85, 85)
  background-color #fff9f9
.showAll
  width 100%
  color $Hbgc
  line-height .78rem
  text-align center
</style>



