import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let defaultCity = '广州'
try {
  if (localStorage.getItem('currentCity')) {
    defaultCity = localStorage.getItem('currentCity')
  }
} catch (e) {}

export default new Vuex.Store({
  state: {
    city: defaultCity
  },
  actions: {
    actionChangeCity (ctx, city) {
      ctx.commit('mutationChangeCity', city)
    }
  },
  mutations: {
    mutationChangeCity (state, city) {
      state.city = city
      localStorage.setItem('currentCity', city)
    }
  }
})



