<template>
  <div class="city">
    <city-header :cities="cities"></city-header>
    <city-order :cities="cities" :hotCities="hotCities"></city-order>
  </div>
</template>

<script>
import CityHeader from './components/Header'
import CityOrder from './components/Order'
import axios from 'axios'
export default {
  name: 'City',
  data () {
    return {
      cities: {},
      hotCities: []
    }
  },
  methods: {
    getData () {
      axios.get('./api/city.json').then(res => {
        const data = res.data.data
        this.cities = data.cities
        this.hotCities = data.hotCities
      })
    }
  },
  components: {
    CityHeader,
    CityOrder
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="stylus" scoped>

</style>



