<template>
  <div class="nav">
    <router-link to="/">
      <div class="nav-back" v-show="isNavShow">
        <i class="iconfont" style="color: #fff">&#xe624;</i>
      </div>
    </router-link>
    <div class="nav-fixed" :style="navStyle" v-show="!isNavShow">
      <router-link to="/">
        <div class="icon">
          <i class="iconfont">&#xe624;</i>
        </div>
      </router-link>
      <h1>世界花卉大观园</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailNavigation',
  data () {
    return {
      timer: null,
      isNavShow: true,
      navStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    scroll () {
      if (!this.timer) {
        this.timer = setTimeout(() => {
          let top = document.documentElement.scrollTop
          if (top > 0) {
            this.isNavShow = false
            let opacity = top / 150
            opacity = opacity >= 1 ? 1 : opacity
            this.navStyle = { opacity }
          } else {
            this.isNavShow = true
          }
          this.timer = null
          clearTimeout(this.timer)
        }, 30)
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.scroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.scroll)
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.nav
  position absolute
  top .1rem
  left .1rem
  .nav-back
    width .72rem
    line-height .72rem
    text-align center
    background-color rgba(0, 0, 0, .8)
    border-radius 50%
  .nav-fixed
    position fixed
    top 0
    left 0
    width 100%
    height .88rem
    color #fff
    line-height .88rem
    text-align center
    background-color $Hbgc
    z-index 99
    .icon
      position absolute
      width .8rem
      color #fff
      z-index 99
</style>



