<template>
  <div class="icons">
    <swiper :options="swiperOption" style="height:3.7rem">
      <swiper-slide v-for="(page, index) in pages" :key="index">
        <div class="icons-item" v-for="item in page" :key="item.id">
          <a href="javascript:;">
            <div class="icons-item-img">
              <img :src="item.imgUrl" :alt="item.text">
            </div>
            <p class="icons-item-content">{{item.text}}</p>
          </a>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination" v-if="pages.length !== 1"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    iconList: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      let arr = []
      this.iconList.forEach((item, index) => {
        let page = Math.floor(index / 8)
        if (!arr[page]) {
          arr[page] = []
        }
        arr[page].push(item)
      })
      return arr
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.icons >>> .swiper-pagination-bullet
  width .12rem
  height .12rem
.icons >>> .swiper-pagination-bullet-active
  background-color rgba(0,175,190,.8)
.icons
  overflow hidden
  height 0
  padding-bottom 3.7rem
  background-color $bgc
  .icons-item
    float left
    width 25%
    margin-top .12rem
    text-align center
    .icons-item-img
      display inline-block
      img
        width 1.1rem
    .icons-item-content
      margin-top .12rem
      overhide()
</style>



