<template>
  <div class="foot">
    <div class="foot-price">
      <span class="price">票面价</span>是指通过景区指定窗口售卖的纸质门票上标注的价格
    </div>
    <div class="foot-type border-bottom">
      <ul class="type">
        <li>
          <div class="icon air"></div>
          <span class="text">机票</span>
        </li>
        <li>
          <div class="icon hotel"></div>
          <span class="text">酒店</span>
        </li>
        <li class="type-house">
          <div class="icon house"></div>
          <span class="text">公寓</span>
        </li>
        <li class="type-more">
          <div class="iconfont"></div>
          <a class="more" href="javascript:;">更多</a>
        </li>
      </ul>
      <ul class="operation">
        <li>
          <a href="javascript:;">登录</a>
        </li>
        <li>
          <a href="javascript:;">我的订单</a>
        </li>
        <li>
          <a href="javascript:;">最近浏览</a>
        </li>
        <li>
          <a href="javascript:;">关于我们</a>
        </li>
      </ul>
    </div>
    <div class="foot-version">
      <ul class="version">
        <li class="v-mobile"><a href="javascript:;">触屏版</a></li>
        <li class="v-computed"><a href="javascript:;">电脑版</a></li>
      </ul>
      <p class="foot-copyright">
        <span>Qunar 京ICP备05021087</span>
        <a href="javascript:;">意见反馈</a>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeFooter'
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.foot-price
  padding .14rem .1rem
  font-size .24rem
  background-color $bgc
  .price
    font-weight bold
.foot-type
  padding .16rem 0 .24rem .38rem
  &::before
    border-bottom-color #cacaca
  .type
    display flex
    justify-content center
    .type-house
      position relative
      &::after
        content '|'
        position absolute
        right 0
        top 0
        color $c9e
    .type-more
      padding-left .28rem
    .icon
      float left
      width .44rem
      height .44rem
      background url('http://source.qunarzz.com/site/images/wap/touch/images/v2/images2x/nav_7.png') 0 0/3.5rem 1rem no-repeat
    li
      padding-right .44rem
    .air
      background-position
    .hotel
      background-position -0.5rem 0
    .house
      background-position -3rem 0
    .text
      font-size .24rem
      padding-left .08rem
      color $c9e
      text-decoration underline
      vertical-align middle
    .arrow
      float left
      width .24rem
      height .24rem
    .more
      color $c9e
      vertical-align middle
  .operation
    display flex
    margin-top .26rem
    li:not(:first-child)
      margin-left .36rem
    a
      color $Hbgc
.foot-version
  .version
    display flex
    justify-content center
    padding-top .2rem
    .v-mobile
      margin-right .6rem
    .v-computed
      a
        color $Hbgc
.foot-copyright
  padding-top .16rem
  padding-bottom .16rem
  color $c9e
  text-align center
  a
    color $c9e
</style>



