<template>
  <div class="weekend">
    <div class="weekend-head">周末去哪儿</div>
    <div class="weekend-list" v-for="item of weekendList" :key="item.id">
      <div class="list-img">
        <img :src="item.imgUrl" alt="">
      </div>
      <div class="list-desc">
        <p class="name">{{item.title}}</p>
        <p class="desc">{{item.desc}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    weekendList: {
      type: Array
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.weekend-head
  padding .26rem .24rem
.weekend-list
  margin-bottom .1rem
  background-color $bgc
.list-img
  img
    width 100%
.list-desc
  padding .24rem 0 .28rem .18rem
  .name
    padding-right 1.4rem
    font-size .28rem
    overhide()
  .desc
    padding-right 1.4rem
    margin-top .16rem
    font-size .24rem
    overhide()
</style>



