<template>
  <div class="head">
    <div class="back">
      <i class="iconfont back-icon">&#xe624;</i>
    </div>
    <div class="input">
      <i class="iconfont search-icon">&#xe632;</i>
      输入城市/景点/游玩主题
    </div>
    <router-link to="/city">
      <div class="city">
        {{this.city}}
        <i class="iconfont down-icon">&#xe607;</i>
      </div>
    </router-link>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'HomeHeader',
  data () {
    return {

    }
  },
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style scoped lang="stylus">
@import '~@/assets/styles/common.styl'
.head
  display flex
  width 100%
  height .88rem
  background-color $Hbgc
  align-items center
  .back
    width .6rem
    height 100%
    line-height .88rem
    text-align center
    margin-right .18rem
    .back-icon
      color #fff
  .input
    height .6rem
    line-height .6rem
    padding-left .18rem
    color #ccc
    background-color #fff
    border-radius .1rem
    flex 1
  .city
    color #fff
    margin-left .22rem
    margin-right .24rem
    .down-icon
      color #fff
      font-size .28rem
</style>



