<template>
  <div class="other">
    <div class="other-head border-bottom">看了该景点的人还看了</div>
    <div class="other-list">
      <div class="other-detail">
        <div class="o-d-left">
          <img class="img" src="http://img1.qunarzz.com/sight/p0/1505/8d/8d1f6d86b2c5926e.water.jpg_228x168_490d8b32.jpg" alt="">
        </div>
        <div class="o-d-right border-bottom">
          <div class="name-price">
            <div class="name">
              北京大观园<span>(3A)</span>
            </div>
            <div class="price">
              ¥<em class="price-num">33.7</em><span class="price-start">起</span>
            </div>
          </div>
          <p class="grade">
            <span>★★★★★</span>
            <span class="grade-num">2282条评论</span>
          </p>
          <p class="place">北京·西城区</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailOther'
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.other
  margin-bottom .2rem
  background-color #fff
.other-head
  position relative
  text-indent .2rem
  line-height .88rem
  padding 0 .2rem
  &::after
    content ''
    position absolute
    top 0
    bottom 0
    left .2rem
    margin auto
    width: .06rem;
    height: .25rem;
    background: #1ba9ba;
    border-radius: .04rem;
.other-list
  .other-detail
    display flex
  .o-d-left
    padding .2rem .2rem
  .img
    width 1.62rem
    height 1.62rem
.o-d-right
  position relative
  flex 1
  padding .2rem .2rem .2rem 0
  .name-price
    display flex
    justify-content space-between
    .name
      font-size .3rem
      span
        color $c9e
    .price
      color $ff8
      font-size .24rem
      .price-num
        font-size .36rem
      .price-start
        color #000
  .grade
    position absolute
    top 1rem
    left 0
    .grade-num
      margin-left .08rem
      font-size .24rem
      color $c9e
  .place
    position absolute
    bottom .2rem
    left 0
    color $c9e
    font-size .24rem
</style>



