<template>
  <div class="detail">
    <detail-banner></detail-banner>
    <detail-navigation></detail-navigation>
    <detail-baseinfo></detail-baseinfo>
    <detail-list></detail-list>
    <detail-comment></detail-comment>
    <detail-other></detail-other>
    <detail-foot></detail-foot>
  </div>
</template>

<script>
import detailBanner from './components/banner'
import detailNavigation from './components/navigation'
import detailBaseinfo from './components/baseinfo'
import detailList from './components/list'
import detailComment from './components/comment'
import detailFoot from '@/common/footer'
import detailOther from './components/othersee'
export default {
  name: 'Detail',
  components: {
    detailBanner,
    detailNavigation,
    detailBaseinfo,
    detailList,
    detailComment,
    detailFoot,
    detailOther
  }
}
</script>

<style lang="stylus" scoped>
.detail
  position relative
  background-color #f5f5f5
</style>



