<template>
  <div class="search">
    <input type="text" style="caret-color:#2bb6ce;" placeholder="请输入城市" v-model="text" @focus="handleClick" ref="input">
    <div class="delete" v-show="text" @click="handleDeleteVal">
      <i class="iconfont" style="color: #000;font-size: .24rem">&#xe604;</i>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      text: '',
      timer: null,
      sign: false
    }
  },
  methods: {
    handleClick () {
      !this.text && this.$emit('changeshadestatus', true)
    },
    handleDeleteVal () {
      this.text = ''
      this.$refs.input.focus()
    }
  },
  watch: {
    text (val) {
      if (!this.timer) {
        this.timer = setTimeout(() => {
          if (val !== '') {
            this.sign = false
            // 搜索框有值
            const arr = []
            for (let key in this.cities) {
              this.cities[key].forEach(item => {
                if (item.spell.indexOf(val) > -1 || item.name.indexOf(val) > -1) {
                  arr.push(item.name)
                }
              })
            }
            this.$emit('changeval', arr)
            this.$emit('changeshadestatus', false)
            this.$emit('changesearchtatus', true)
          } else {
            // 搜索框没有值
            !this.sign && this.$emit('changeshadestatus', true)
            this.$emit('changesearchtatus', false)
          }
          this.timer = null
          clearTimeout(this.timer)
        }, 30)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.search
  position relative
  width 70%
  margin 0 auto
  input
    width 100%
    padding 0 .2rem
    color $c9e
    font-size .24rem
    line-height .48rem
    z-index 10
    border-radius .1rem
    box-sizing border-box
  .delete
    position absolute
    right 0
    top 0
    bottom 0
    width .5rem
    margin auto 0
    vertical-align middle
</style>



