<template>
  <div class="position border-top">
    <div class="position-left border-right">
      <i class="iconfont">&#xe60c;</i>
      定位失败
    </div>
    <div class="position-right">
      <i class="iconfont">&#xe642;</i>
      必游榜单
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomePosition'
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.position
  display flex
  width 100%
  text-align center
  background-color $bgc
  .position-left
    flex 1
    line-height .96rem
  .position-right
    flex 1
    line-height .96rem
</style>



