<template>
  <div class="order" ref="wrap">
    <div>
      <div class="current-city">
        <div class="cc-head">当前城市</div>
        <div class="cc-content">
          <div class="city border-right">{{this.$store.state.city}}</div>
        </div>
      </div>
      <div class="hot-city">
        <div class="hc-head">热门城市</div>
        <div class="hc-list">
          <ul class="">
            <li class="border-bottom" v-for="item of hotCities" :key="item.id" @click="changeCity(item.name)">
              <a href="javascript:;">{{item.name}}</a>
            </li>
          </ul>
        </div>
      </div>
      <div class="alphabet-order">
        <div class="o-head">字母排序</div>
        <div class="o-list">
          <ul>
            <li v-for="(items, key) of cities" :key="key" @click="handleClick">
              <a href="javascript:;">{{key}}</a>
            </li>
          </ul>
        </div>
      </div>
      <div class="alphabet" v-for="(items, key) of cities" :key="key">
        <div class="a-head" :ref="key">{{key}}</div>
        <div class="a-detail">
          <ul>
            <li class="border-bottom" v-for="item of items" :key="item.id" @click="changeCity(item.name)">
              <a href="javascript:;">{{item.name}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="navigator">
      <ul ref="ul">
        <li v-for="item of navigator" :key="item" @click="handleClick" @touchstart.prevent="handleTouchStart" @touchmove="handleTouchMove" @touchend="handleTouchEnd">
          {{item}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import {mapActions} from 'vuex'
export default {
  name: 'CityOrder',
  props: {
    cities: Object,
    hotCities: Array
  },
  data () {
    return {
      timer: null,
      timer1: null,
      status: false
    }
  },
  methods: {
    changeCity (city) {
      // this.$store.dispatch('changeHotCity', city)
      this.actionChangeCity(city)
      this.$router.push('/')
    },
    ...mapActions(['actionChangeCity']),
    handleClick (ev) {
      this.timer && clearTimeout(this.timer)
      let val = ev.target.innerText
      if (val === '#') {
        this.timer = setTimeout(() => {
          this.scroll.scrollTo(0, 0, 0)
        })
      } else {
        let target = this.$refs[val][0]
        this.timer = setTimeout(() => {
          this.scroll.scrollToElement(target, 0)
        }, 16)
      }
    },
    handleTouchStart () {
      this.status = true
    },
    handleTouchMove (ev) {
      if (this.status) {
        // 节流
        if (!this.timer1) {
          this.timer1 = setTimeout(() => {
            // 获取鼠标当前到头部底边的距离
            let H = ev.targetTouches[0].clientY - 76
            // 获取导航栏顶边到头部底边的距离
            let startH = this.$refs.ul.offsetTop
            // 获取导航栏单个字母的高度
            let selfH = ev.target.offsetHeight
            // 获取当前鼠标所指的字母索引
            let index = Math.floor((H - startH) / selfH)
            if (index >= 0 && index < this.navigator.length) {
              let val = this.navigator[index]
              if (val === '#') {
                this.scroll.scrollTo(0, 0, 0)
              } else {
                let target = this.$refs[val][0]
                this.scroll.scrollToElement(target, 0)
              }
            }
            this.timer1 = null
            clearTimeout(this.timer1)
          }, 30)
        }
      }
    },
    handleTouchEnd () {
      this.status = false
    }
  },
  computed: {
    navigator () {
      const obj = Object.assign({}, this.cities, {'#': ''})
      const arr = Object.keys(obj)
      arr.unshift(arr.splice(arr.indexOf('#'), 1)[0])
      return arr
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrap, {
      click: true
    })
  },
  activated () {
    this.scroll.refresh()
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.order
  position absolute
  left 0
  right 0
  top 1.52rem
  bottom 0
  overflow hidden
  background-color #f5f5f5
  .current-city
    .cc-head
      padding .22rem 0 .18rem .3rem
      font-size .24rem
      color $c9e
    .cc-content
      display flex
      height .9rem
      background-color $bgc
      .city
        width 33.33%
        line-height .9rem
        text-align center
        &::before
          border-color #ddd
  .hot-city
    .hc-head
      padding .22rem 0 .18rem .3rem
      font-size .24rem
      color $c9e
    .hc-list
      position relative
      ul
        position relative
        display flex
        width 100%
        flex-wrap wrap
        background-color $bgc
        &::before
          content ''
          position absolute
          left 33.33%
          top 0
          width 33.33%
          height 100%
          border-left .02rem solid #ddd
          border-right .02rem solid #ddd
        li
          width 33.33%
          text-align center
          line-height .9rem
  .o-head
    padding .22rem 0 .18rem .3rem
    font-size .24rem
    color $c9e
  .o-list
    ul
      display flex
      padding .3rem 0
      background-color $bgc
      flex-wrap wrap
      li
        width 16.66666666666667%
        text-align center
        line-height .9rem
        a
          display block
          width 100%
          height 100%
  .alphabet
    .a-head
      padding .22rem 0 .18rem .3rem
      font-size .24rem
      color $c9e
    .a-detail
      ul
        position relative
        display flex
        flex-wrap wrap
        background-color $bgc
        &::before
          content ''
          position absolute
          left 25%
          top 0
          width 25%
          height 100%
          border-left .02rem solid #ddd
          border-right .02rem solid #ddd
        &::after
          content ''
          position absolute
          left 50%
          width 25%
          height 100%
          border-right .02rem solid #ddd
        li
          width 25%
          line-height .9rem
          text-align center
          z-index 10
          a
            display block
            width 100%
            height 100%
  .navigator
    display flex
    position absolute
    top 0
    right 0
    bottom 0
    width .4rem
    margin auto 0
    color $c9e
    text-align center
    align-items center
    justify-content center
    z-index 999
    ul
      width 100%
</style>



