<template>
  <div class="hotList">
    <div class="hotList-head">
      <img class="hotList-img" src="https://imgs.qunarzz.com/piao/fusion/1711/16/bfbb9874e8f11402.png" alt="本周热门榜单">
      <span class="hotList-title">本周热门榜单</span>
      <a class="hotList-all" href="javascript:;">
        <span>全部榜单</span>
        <i class="iconfont">&#xe600;</i>
      </a>
    </div>
    <div class="hotList-banner">
      <swiper :options="swiperOption" class="wrap">
        <swiper-slide v-for="(item, index) in hotList" :key="item.id" class="slide-style">
          <a href="javascript:;" class="all">
            <div class="hotList-banner-all">
              <div class="hotList-banner-topImg" v-if="index < 3">
                <img :src="topImgList[index]" :alt="index+1">
              </div>
              <div class="hotList-banner-img">
                <img :src="item.imgUrl" :alt="item.desc" >
              </div>
              <p class="hotList-banner-desc">{{item.desc}}</p>
              <p class="hotList-banner-price">
                <span class="hotList-banner-price">¥<span class="num">{{item.price}}</span></span>起
              </p>
            </div>
          </a>
        </swiper-slide>
        <div class="swiper-scrollbar" slot="scrollbar" style="height:3px;"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeHotList',
  props: {
    hotList: Array
  },
  data () {
    return {
      swiperOption: {
        freeMode: true,
        slidesPerView: 'auto',
        spaceBetween: 6,
        freeModeMomentumBounce: false,
        onTouchMove: function () {
          document.querySelector('.swiper-scrollbar').style.backgroundColor = '#fff'
        }
      },
      topImgList: ['http://img1.qunarzz.com/piao/fusion/1710/ab/159673b63e6ca702.png', 'http://img1.qunarzz.com/piao/fusion/1710/2d/36d0c4adaebbbc02.png', 'http://img1.qunarzz.com/piao/fusion/1710/67/edc47ffef9e96b02.png']
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.hotList >>> .swiper-scrollbar-drag
  background-color rgba(0, 0, 0, .3)
.hotList
  width 100%
  height 4.2rem
  margin-top .22rem
  background-color $bgc
  .hotList-head
    padding .24rem .2rem
    .hotList-img
      width .3rem
      vertical-align middle
    .hotList-title
      vertical-align text-bottom
    .hotList-all
      float right
  .hotList-banner
    text-align center
    .wrap
      padding: .24rem .2rem
      .slide-style
        width 2rem
    .wrap .all
      display block
      width 100%
      .hotList-banner-all
        position relative
        .hotList-banner-topImg
          position absolute
          top -0.04rem
          left 0
          img
            width .84rem
        .hotList-banner-desc
          margin-top .12rem
          overhide()
        .hotList-banner-img
          img
            width 2rem
        p
          font-size .24rem
        .hotList-banner-price
          line-height .38rem
          .num
            font-size .28rem
          span
            color #ff8300
</style>



