<template>
  <div class="carousel" @click="handleCarouselClick">
    <div class="carousel-content">
      <swiper :options="swiperOption">
        <swiper-slide>
          <img class="carousel-img" src="http://img1.qunarzz.com/sight/p0/1604/1e/1e75f8e1af9fe81f90.img.jpg_600x330_aeba2c96.jpg" alt="">
        </swiper-slide>
        <swiper-slide>
          <img class="carousel-img" src="http://img1.qunarzz.com/sight/p0/1409/19/adca619faaab0898245dc4ec482b5722.jpg_600x330_f922b488.jpg" alt="">
        </swiper-slide>
        <swiper-slide>
          <img class="carousel-img" src="http://img1.qunarzz.com/sight/p0/1604/1e/1e75f8e1af9fe81f90.img.jpg_600x330_aeba2c96.jpg" alt="">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Carousel',
  data () {
    return {
      swiperOption: {
        effect: 'fade',
        observer: true,
        observeParents: true,
        pagination: '.swiper-pagination',
        paginationType: 'fraction'
      }
    }
  },
  methods: {
    handleCarouselClick () {
      this.$emit('carouselHide')
    }
  }
}
</script>

<style lang="stylus" scoped>
.swiper-container
  width 100%
  overflow inherit
.carousel
  display flex
  position fixed
  left 0
  right 0
  top 0
  bottom 0
  background-color #000
  align-items center
  z-index 99
  .carousel-content
    display flex
    width 100%
    height 5rem
    align-items center
    .carousel-img
      width 100%
    .swiper-pagination
      bottom -3.4rem
      color #fff
</style>



