<template>
  <div class="comment">
    <div class="comment-head border-bottom">用户评论</div>
    <div class="comment-detail border-bottom">
      <div class="c-d-star">
        <div class="star">★★★★★</div>
        <div class="user">
          <span class="name">猪*心</span>
          <span class="date">2019-03-16</span>
        </div>
      </div>
      <p class="c-d-content" :style="contentStyle">
        现在是3月份，外面的花还没有开，花房里面的花可以看。我是从北门进去的，貌似感激南门是正门呢，从南门进，先是一个维也纳广场，然后是一个湖，湖里面有黑天鹅，然后有4-5个花房，从蔬菜水果那个花房进，然后是热带植物，沙漠植物，花卉，还有一个忘记是什么名字了。花房里面有桌椅板凳，累了可以坐下休息吃点东西。园中还有一些艺术作品，有投票箱🗳️，把票根放进喜欢的作品前面的小箱子里就可以了！取票方便，输入12位验证码，确认就自动出票了。
      </p>
      <div class="arrow" @click="handleArrowClick">
        <i class="iconfont" style="font-size:.24rem" v-show="!sign">&#xe601;</i>
        <i class="iconfont" style="font-size:.24rem" v-show="sign">&#xe64a;</i>
      </div>
      <ul class="c-d-imgs">
        <li @click="handleImgCiick">
          <img src="http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/fusion/20193/f530398307d434cc6fb39cac53056f08.jpg_228x168_8315b81b.jpg" alt="">
        </li>
      </ul>
    </div>
    <carousel v-show="isCarouselShow" @carouselHide="carouselHide"></carousel>
    <div class="show-all">查看全部评论<i class="iconfont">&#xe600;</i></div>
  </div>
</template>

<script>
import carousel from '@/common/carousel'
export default {
  name: 'DetailComment',
  data () {
    return {
      isCarouselShow: false,
      sign: false,
      contentStyle: {
        height: '2.1rem',
        overflow: 'hidden'
      }
    }
  },
  methods: {
    handleArrowClick () {
      if (!this.sign) {
        this.contentStyle = {
          height: 'auto',
          overflow: 'visible'
        }
        this.sign = true
      } else {
        this.contentStyle = {
          height: '2.1rem',
          overflow: 'hidden'
        }
        this.sign = false
      }
    },
    handleImgCiick () {
      this.isCarouselShow = true
    },
    carouselHide () {
      this.isCarouselShow = false
    }
  },
  components: {
    carousel
  }
}
</script>

<style lang="stylus" scoped>
.comment
  margin-bottom .2rem
  background-color #fff
.comment-head
  position relative
  text-indent .2rem
  line-height .88rem
  padding 0 .2rem
  &::after
    content ''
    position absolute
    top 0
    bottom 0
    left .2rem
    margin auto
    width: .06rem;
    height: .25rem;
    background: #1ba9ba;
    border-radius: .04rem;
.comment-detail
  padding .1rem .2rem .3rem
  .c-d-star
    display flex
    line-height .6rem
    justify-content space-between
    .user
      font-size .24rem
      .name
        margin-right .08rem
  .c-d-content
    height 2.1rem
    font-size .26rem
    color #616161
    line-height .42rem
    overflow hidden
  .arrow
    text-align center
    line-height .48rem
  .c-d-imgs
    display flex
    flex-wrap wrap
    li
      width 33.33%
      text-align center
      padding 0 .07rem
      margin-bottom .1rem
      box-sizing border-box
      img
        width 100%
.show-all
  line-height .8rem
  text-align center
</style>



