<template>
  <div class="head">
    <div class="h-title">
      <router-link to="/" @click.native="backClick">
        <div class="back">
          <i class="iconfont">&#xe624;</i>
        </div>
      </router-link>
      <city-search ref="citySearch" :cities="cities" @changeshadestatus="handleChangeShadeStatus" @changeval="handleChangeVal" @changesearchtatus="handleChangeSearchStatus"></city-search>
    </div>
    <div class="h-type">
      <ul class="h-type-wrap border">
        <li>境内</li>
        <li>境外-港澳台</li>
      </ul>
    </div>
    <div class="wrap" ref="searchList" v-show="isSearchListShow">
      <ul class="search-result" >
        <li class="border-bottom" v-for="(item, index) of searchResult" :key="index" @click="changeCity(item)">
          {{item}}
        </li>
        <li v-show="!searchResult.length">搜索无结果</li>
      </ul>
    </div>
    <div class="shade" @click="handleShadeClick" v-show="isShadeShow"></div>
  </div>
</template>

<script>
import citySearch from './Search'
import Bscroll from 'better-scroll'
import {mapActions} from 'vuex'
export default {
  name: 'CityHeader',
  props: {
    cities: Object
  },
  data () {
    return {
      isShadeShow: false,
      isSearchListShow: false,
      searchResult: []
    }
  },
  methods: {
    handleChangeShadeStatus (bool) {
      // 遮罩是否显示
      this.isShadeShow = bool
    },
    handleChangeSearchStatus (bool) {
      // 搜索结果是否显示:
      // 搜索框有内容时显示, 无内容时隐藏
      this.isSearchListShow = bool
    },
    handleChangeVal (arr) {
      this.searchResult = arr
    },
    changeCity (city) {
      this.actionChangeCity(city)
      this.$router.push('/')
      // this.isSearchListShow = false
      this.$refs.citySearch.text = ''
      this.$refs.citySearch.sign = true
    },
    ...mapActions(['actionChangeCity']),
    handleShadeClick () {
      // 点击遮罩让遮罩隐藏
      this.isShadeShow = false
    },
    backClick () {
      this.isShadeShow = false
    }
  },
  components: {
    citySearch
  },
  mounted () {
    this.scrolls = new Bscroll(this.$refs.searchList, {
      click: true
    })
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.head
  background-color $Hbgc
  .h-title
    position relative
    width 100%
    height .88rem
    color $bgc
    font-size .32rem
    text-align center
    line-height .88rem
    .back
      position absolute
      top 0
      left 0
      width .88rem
      height .88rem
      color $bgc
      margin auto
  .h-type
    padding 0 1rem .2rem 1rem
    .h-type-wrap
      display flex
      border-color $bgc
      li
        flex 1
        line-height .44rem
        text-align center
      li:first-child
        color $Hbgc
        background-color $bgc
      li:last-child
        color $bgc
        background-color $Hbgc
  .wrap
    position absolute
    top 1.52rem
    bottom 0
    left 0
    right 0
    overflow hidden
    background-color #fff
    z-index 999
    .search-result
      li
        padding .18rem
  .shade
    position absolute
    top 1.52rem
    bottom 0
    left 0
    right 0
    background-color rgba(0, 0, 0, .3)
    z-index 999
</style>



