<template>
  <div class="home">
    <home-header></home-header>
    <home-swiper :swiperList="swiperList"></home-swiper>
    <home-icons :iconList="iconList"></home-icons>
    <home-position></home-position>
    <home-hotlist :hotList="hotList"></home-hotlist>
    <home-guess :guessList="guessList"></home-guess>
    <home-weekend :weekendList="weekendList"></home-weekend>
    <home-footer></home-footer>
  </div>
</template>

<script>
import HomeHeader from './components/Header'
import HomeSwiper from './components/Swiper'
import HomeIcons from './components/Icons'
import HomePosition from './components/Position'
import HomeHotlist from './components/HotList'
import HomeGuess from './components/Guess'
import HomeWeekend from './components/Weekend'
import HomeFooter from './components/Footer'
import axios from 'axios'
import {mapState} from 'vuex'
export default {
  name: 'Home',
  data () {
    return {
      lastCity: '',
      swiperList: [],
      iconList: [],
      hotList: [],
      guessList: [],
      weekendList: []
    }
  },
  computed: {
    ...mapState(['city'])
  },
  methods: {
    getData () {
      axios.get('./api/index.json' + '?city=' + this.city).then((res) => {
        const data = res.data.data
        this.swiperList = data.swiperList
        this.iconList = data.items
        this.hotList = data.hotList
        this.guessList = data.guessList
        this.weekendList = data.weekendList
      })
    }
  },
  components: {
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomePosition,
    HomeHotlist,
    HomeGuess,
    HomeWeekend,
    HomeFooter
  },
  mounted () {
    this.getData()
    this.lastCity = this.city
  },
  activated () {
    if (this.lastCity !== this.city) {
      this.getData()
      this.lastCity = this.city
    }
  }
}
</script>

<style lang="stylus" scoped>
.home
  background-color #f5f5f5
</style>



