<template>
  <div class="baseinfo">
    <div class="baseinfo-top border-bottom">
      <div class="grade border-right">
        <a href="javascript:;" class="g-detail">
          <i class="iconfont" style="font-size:.24rem">&#xe600;</i>
        </a>
        <p class="grade-top"><span class="score">5.0</span>分<span class="text">超赞</span></p>
        <p class="grade-bottom"><span>381555条评论</span><span class="strategy">93条攻略</span></p>
      </div>
      <div class="intro">
        <a href="javascript:;" class="i-detail">
          <i class="iconfont" style="font-size:.24rem">&#xe600;</i>
        </a>
        <p class="intro-top">景点简介</p>
        <p class="intro-bottom">开始时间.贴士</p>
      </div>
    </div>
    <div class="baseinfo-bottom">
      <a href="">
        <i class="iconfont">&#xe60c;</i>
        <span>北京市东城区景山前街4号</span>
        <i class="iconfont arrow">&#xe600;</i>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailBaseInfo'
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/common.styl'
.baseinfo
  padding .1rem .2rem 0
  margin-bottom .2rem
  background-color #fff
  .baseinfo-top
    display flex
    padding-bottom .2rem
    .grade, .intro
      position relative
      flex 1
      .g-detail, .i-detail
        position absolute
        right 0
        top 0
        bottom 0
        width .24rem
        height .24rem
        margin auto 0
      .strategy
        padding .2rem
      .g-detail
        right .2rem
      .grade-top
        color $ff8
        .score
          font-size .44rem
        .text
          margin-left .2rem
      .grade-bottom, .intro-bottom
        padding-top .08rem
        color $c9e
        font-size .24rem
      .intro-top
        line-height .44rem
        text-indent .2rem
      .intro-bottom
        text-indent .2rem
  .baseinfo-bottom
    position relative
    padding .2rem 0
    .arrow
      position absolute
      right 0
      top 0
      bottom 0
      margin auto 0
      width .24rem
      height .24rem
      font-size .24rem
</style>



