<template>
  <div>
    <div class="banner" @click="carouselShow">
      <div class="banner-img">
        <img src="http://img1.qunarzz.com/sight/p0/1604/1e/1e75f8e1af9fe81f90.img.jpg_600x330_aeba2c96.jpg" alt="">
      </div>
      <div class="banner-count">
        <i class="iconfont">&#xe66c;</i>
        <span>39</span>
      </div>
      <div class="banner-desc">
        <p>世界花卉大观园(AAAA景区)</p>
      </div>
    </div>
    <carousel v-show="isCarouselShow" @carouselHide="carouselHide"></carousel>
  </div>
</template>

<script>
import carousel from '@/common/carousel'
export default {
  name: 'DetailBanner',
  data () {
    return {
      isCarouselShow: false
    }
  },
  methods: {
    carouselHide (ev) {
      this.isCarouselShow = false
    },
    carouselShow () {
      this.isCarouselShow = true
    }
  },
  components: {
    carousel
  }
}
</script>

<style lang="stylus" scoped>
.banner
  position relative
  color #fff
  overflow hidden
  height 0
  padding-bottom 55%
  .banner-img
    img
      width 100%
  .banner-count
    position absolute
    left .2rem
    bottom .86rem
  .banner-desc
    position absolute
    bottom .36rem
    left .2rem
    font-size .36rem
</style>



